.stack{
  display: inline-grid;
  grid-template-columns: 3px 4px 1fr 4px 3px;
  grid-template-rows: 3px 4px 1fr 4px 3px;
}
.stack > *{
  height: 100%;
  width: 100%;
}
.stack > *:nth-child(n + 2){
  width: 100%;
  opacity: 70%;
}
.stack > *:nth-child(2){
  z-index: 2;
  opacity: 90%;
